.ripple-ink{display:block;position:absolute;left:0;top:0;right:0;bottom:0;overflow:hidden;border-radius:inherit;-webkit-mask-image:-webkit-radial-gradient(circle, #fff, #000)}.ripple-ink .ripple{position:absolute;width:0;height:0;pointer-events:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border-radius:50%;background-color:currentColor;background-clip:padding-box;-webkit-transform:scale(0);transform:scale(0);-webkit-transition:opacity 0.4s ease-out, -webkit-transform 0.4s ease-out;transition:opacity 0.4s ease-out, -webkit-transform 0.4s ease-out;transition:transform 0.4s ease-out, opacity 0.4s ease-out;transition:transform 0.4s ease-out, opacity 0.4s ease-out, -webkit-transform 0.4s ease-out}.ripple-ink .ripple.held{-webkit-transform:scale(1);transform:scale(1)}.ripple-ink .ripple.done{opacity:0 !important}



